<template>
  <div>
    <div class="body__overlay"></div>
    <div class="offset__wrapper">
      <div class="search__area">
        <div class="container">
          <div class="row">
            <div class="col-md-12">
              <div class="search__inner">
                <form action="#" method="get">
                  <input placeholder="Search here... " type="text">
                  <button type="submit"></button>
                </form>
                <div class="search__close__btn">
                  <span class="search__close__btn_icon"><i class="zmdi zmdi-close"></i></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <section class="htc__product__area ptb--130 bg__white">
      <div class="container">
        <div class="htc__product__container">
          <div class="row">
            <div class="col-md-12">
              <div class="product__menu">
                <template v-for="(menu, key) in menus">
                  <button :class="menu.active?'is-checked': ''"
                          @click="switchProduct(menu)"
                          :key="key">{{ menu.label }}
                  </button>
                </template>
              </div>
            </div>
          </div>
          <div class="row product__list"
               v-if="products.length>0">
            <template v-for="(product, key) in products">
              <div class="col-md-3 single__pro col-lg-3 col-md-4 col-sm-12 cat--4"
                   :key="key">
                <product-item :product="product"/>
              </div>
            </template>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import ProductItem from "../components/ProductItem";

  export default {
    name: 'Home',
    metaInfo: {
      title: 'ហេងហេងឃែរ',
      titleTemplate: 'ទំព័រដើម | %s'
    },
    components: {ProductItem},
    data() {
      return {
        menus: [
          {id: -1, label: 'ទាំងអស់', active: true},
          {id: 2, label: 'ឡេលាប', active: false},
          {id: 1, label: 'សាប៊ូលាងមុខ និងដុសខ្លួន', active: false},
          {id: 3, label: 'ក្រែម', active: false},
        ],
        items: [
          {category_id: 1, name: 'សាប៊ូលាងមុខ និង ដុសខ្លួន', discount: '20.00', price: '10.00', image: '01.jpg'},
          {category_id: 2, name: 'ឡេលាបពេលថ្ងៃ', discount: '16.00', price: '10.00', image: '02.jpg'},
          {category_id: 2, name: 'ឡេលាបពេលយប់', discount: '16.00', price: '10.00', image: '03.jpg'},
          {category_id: 3, name: 'ក្រែមលាបបបូរមាត់', discount: '16.00', price: '10.00', image: '04.jpg'},
        ],
        selected: {id: -1, label: 'ទាំងអស់', active: true},
        products: [],
      }
    },
    watch: {
      selected() {
        if (this.selected.id === -1) {
          this.products = this.items
        } else {
          this.products = this.items.filter((item) => item.category_id === this.selected.id)
        }
      }
    },
    methods: {
      switchProduct(menu) {
        this.menus.forEach((menu) => {
          if (menu.active) {
            menu.active = false
          }
        })
        let foundMenu = this.menus.filter((item) => item === menu)[0]
        foundMenu.active = true
        this.selected = foundMenu
      }
    },
    mounted() {
      this.products = this.items
    }
  }
</script>
